.our-team-page{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.worker-info{
    margin-top: 4%;
}

.all-workers{
    display: flex;
    width: 920px;
    overflow: auto;
    margin-bottom: 70px;
    padding-bottom: 30px;
}

/*scroll settings for all workers*/
.all-workers::-webkit-scrollbar {
    height: 6px;
}
.all-workers::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.3); 
    border-radius: 10px;
}
.all-workers::-webkit-scrollbar-thumb {
    max-height: 10px;
    
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.5); 
}

.img-our-team-background2{
    width: 30%;
    position: absolute;
    right: 0px;
    top: 80%;
    opacity: 0.7;
}

.worker-scroll-left{
    margin-top: 70px;
    padding-left: 20px;
    cursor: pointer;
    
}
.worker-scroll-right{
    transform: rotate(180deg);
    margin-top: 70px;
    padding-right: 20px;
    cursor: pointer;
}

.all-workers-with-scroll{
    display: flex;
    align-items: flex-start;
}